@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');
@import "abstracts/variables";
@import "components/button";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 60%; }
body, html {
    height: 100%; }
body {
    font-family: 'Play', sans-serif;
    font-weight: 400;
    background: url(../img/background12.jpg) no-repeat center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    // background: linear-gradient(72.01deg, #ffe2c9d3 0%, #ffe2c9d3 50.02%, #d9bea9b2 97.4%)
    overflow-x: hidden; }

.container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden; }

.wrapper {
    position: absolute;
    width: $width-100rem;
    height: $height-60rem;
    &__main {
        width: $width-100rem;
        height: $height-60rem;
        display: flex;
        // background: linear-gradient(145deg, #ffe2c9d3, #d9bea9b2)
        // background: linear-gradient(72.01deg, rgba(243, 74, 74, 0.85) 0%, rgba(247, 105, 105, 0.51) 50.02%, rgba(243, 74, 74, 0.69) 97.4%)
        // background: linear-gradient(145deg,#f34a4ad9, #f7696982)
 } }        // box-shadow: 20px 20px 60px #9a8778, -20px -20px 60px #ffffff
.main {
    &__new-game {
        top: 3rem; }
    &__roll-dice {
        top: 42rem; }
    &__bank {
        top: 50rem; }
    &__dice {
        position: absolute;
        left: 50%;
        top: 25rem;
        transform: translateX(-50%); } }

.pig {
    &__head {
        clip-path: ellipse(50% 50% at 50% 50%);
        position: absolute;
        background-color: $background-head;
        z-index: 2;
        // clip-path: polygon(20% 0, 80% 0, 100% 40%, 90% 80%, 50% 100%, 10% 80%, 0 40%)
 }        // background: linear-gradient(145deg, #f994cd, #d27cad)
    &__ear-left {
        background: linear-gradient(106.51deg, $colors-ear-active 0%, rgba(239, 122, 150, 0.24) 80.54%);
        height: 16rem;
        width: 15rem;
        top: 0rem;
        left: 2.4rem;
        clip-path: polygon(0 0, 100% 30%, 69% 100%);
        transform: rotate(17deg);
        position: absolute;

        background: linear-gradient(355deg,$background-head 30px,rgba(239,122,150,.24) 80.54%); }
    &__ear-left2 {
        background: radial-gradient(43.1% 132.71% at 50% 45%, $colors-ear-active 0%, rgba(237, 76, 123, 0.24) 100%);
        height: 17rem;
        width: 8.5rem;
        top: -1.2rem;
        left: 3.37rem;
        clip-path: polygon(0 0, 100% 100%, 35% 85%);
        position: absolute;
        transform: rotate(11deg);

        background: radial-gradient(43.1% 132.71% at 50% 45%, $background-head 0%, rgba(237, 76, 123, 0.24) 100%); }
    &__ear-right {
        background: linear-gradient(245.18deg, $colors-ear-active 0%, rgba(239, 122, 150, 0.24) 75.98%);
        height: 16rem;
        width: 15rem;
        clip-path: polygon(0 0, 99% 35%, 64% 100%);
        position: absolute;
        right: 3.7rem;
        top: 0rem;
        transform: scaleX(-1) rotate(13deg); }
    &__ear-right2 {
        background: radial-gradient(43.1% 132.71% at 50% 45%, $colors-ear-active 0%, rgba(237, 76, 123, 0.24) 100%);
        height: 17rem;
        width: 8.5rem;
        clip-path: polygon(0 2%, 100% 100%, 35% 85%);
        position: absolute;
        right: 4.5rem;
        top: -1.1rem;
        transform: scaleX(-1) rotate(9deg); } }
.player {
    flex: 50%;
    padding: 12rem 9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.75s;
    filter: blur(1.8px);
    &__name {
        position: relative;
        font-size: 4rem;
        text-transform: uppercase;
        letter-spacing: 1px;
        word-spacing: 2px;
        font-weight: 300;
        margin-bottom: 1rem;
        filter: $drop-shadow-name-player;
        text-shadow: $text-shadow-player;
        color: $colors-text-player; }
    &__score {
        font-size: 8rem;
        font-weight: 300;
        color: $colors-text-score;
        margin-bottom: 8rem;
        filter: $drop-shadow-score-player;
        text-shadow: $text-shadow-score; }
    &_active {
        background-color: $background-head-active;
        filter: blur(0); } }

.current {
    clip-path: ellipse(50% 50% at 50% 50%);
    background: $background-current;
    box-shadow: $box-shadow-current;
    opacity: .9;
    border-radius: 9px;
    color: $colors-text-current;
    text-shadow: $text-shadow-current;
    width: 65%;
    padding: 3.1rem 0 2rem 0;
    text-align: center;
    transition: all 0.75s;
    margin: 1rem;
    &__label {
        text-transform: uppercase;
        margin-bottom: 1rem;
        font-size: 1.8rem; }

    &__point {
        font-size: 3.6rem; } }

.dice {
    height: 12rem;
    filter: $drop-shadow-dice; }

.btn-position {
    position: absolute;
    left: 49%;
    transform: translateX(-50%); }

//Кнопка из спанов крестик
.icon-close {
    position: absolute;
    top: 5%;
    right: 5%;
    & span {
        margin-bottom: 0.9rem;
        // & span:nth-child(1)
        // & span:nth-child(2)
        & span:nth-child(3) {
            margin-bottom: 0; } }
    // &_active
    //  // &__span
    //  // & span:nth-child(1)
    //  // & span:nth-child(2)
 }    //  // & span:nth-child(3)

//Модальное окно стартовое окно
.start-game {
    padding: 3rem;
    border-radius: 10rem;
    position: absolute;
    top: -4rem;
    left: 0;
    width: 100%;
    opacity: 1;
    height: 110%;
    z-index: 3;
    background: $background-modal-window;
    box-shadow: -5px 5px 14px #493543, 5px -5px 14px #614759;
    color: $colors-text-start-game_2;
    font-size: 2rem;
    text-align: center;
    // backface-visibility: hidden
    // opacity: 0
    &__name {
        color: $colors-text-start-game_1;
        margin-top: 0;
        text-align: center;
        font-size: 7rem;
        &_animation {
            animation: rubberband 1.8s steps(8) 2s infinite alternate backwards;
            backface-visibility: hidden;
            perspective: 1000px; } }
    &__plane {
        text-align: center;
        font-size: 4rem;
        margin-bottom: 3rem; }
    &__form {
        text-align: center; }
    &__players-name {
        font-size: 3rem;
        margin-bottom: 3rem; }
    &__block-input {
        width: 44%;
        text-align: center;
        position: relative;
        text-align: center;
        display: inline-block;
        margin-right: 8rem;
        margin-bottom: 3rem;
        &:nth-of-type(2) {
            margin-right: 0; } }
    // &__label-player-1
    // &__input-player-1
    // &__label-player-2
    // &__input-player-2
    &__default-name {
        font-size: 3rem;
        margin-bottom: 3.5rem;
        line-height: 3rem;
        color: $colors-text-default-name-start-game; }
    &__btn-start {
        margin-bottom: 3rem;
        &:active {
            transform: translateY(3px); }
        &_animation {
            animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) 4s both;
            transform: translate3d(0, 0, 0);
            backface-visibility: hidden;
            perspective: 1000px; } }
    &__btn-rule-game {
        margin: 0 3rem;
        &:active {
            transform: translateY(3px); } }
    &_close {
        animation: run .3s ease-in 0s reverse forwards; }
    &_active {
        animation: run .3s ease-out .2s normal backwards; } }

.date-form {
    &__label {
        color: $colors-text-date-form-label;
        position: absolute;
        transform: translate(-50%);
        transform-origin: center;
        top: 20%;
        left: 50%;
        pointer-events: none;
        transition: .3s ease-in-out;
        background: linear-gradient(337deg, #5b4253, #4d3846);
        padding: 1rem;
        opacity: .7;
        text-transform: uppercase;
        font-size: 2.8rem; }
    &__input {
        background: $background-modal-window;
        outline: none;
        width: 70%;
        border: 1rem solid $colors-text-date-form-input;
        border-radius: 3rem;
        padding: 1rem 3.5rem;
        letter-spacing: 0.1rem;
        font-size: 4rem;
        color: $colors-text-date-form-input; } }

.date-form__input:focus + .date-form__label, .date-form__input:not(:placeholder-shown) + .date-form__label {
    top: 0;
    transform: translate(-50%,-50%) scale(.5);
    opacity: 1; }

.rule-game {
    padding: 3rem;
    border-radius: 10rem;
    position: absolute;
    top: -4rem;
    width: 100%;
    opacity: 1;
    height: 110%;
    z-index: 4;
    background: $background-modal-window;
    box-shadow: -5px 5px 14px #493543, 5px -5px 14px #614759;
    color: $colors-text-rule-game_2;
    font-size: 2rem;
    text-align: center;
    backface-visibility: hidden;
    &__title {
        color: $colors-text-rule-game_1;
        margin-top: 0;
        font-size: 6rem; }
    &__tabs {
        position: relative;
        height: 45rem;
        width: 100%; }
    &__tab {
        float: left; }
    &__list {
        font-size: 2.5rem;
        list-style: inside;
        text-align: justify;
        & li {
            margin-bottom: 1.5rem;
            line-height: 5.5rem; } }
    &__dice {
        height: 8rem;
        margin: 0rem 1rem; }
    &__label {
        cursor: pointer;
        background: #c69;
        font-size: 1.2em;
        border-radius: 7px 7px 0 0;
        padding: .5em 1em;
        margin: 0 1px 0 0; }
    &__input {
        position: absolute;
        clip: rect(0,0,0,0);
        &:checked ~ .rule-game__label {
            background: #c99; }
        &:checked ~ .rule-game__label ~ .rule-game__content {
            z-index: 1; } }
    &__content {
        text-align: justify;
        color: $colors-text-start-game_2;
        background: #2F3225;
        font-size: 2.5rem;
        border: 1px solid #c69;
        border-radius: 0 5px 5px;
        padding: 2rem 3rem;
        position: absolute;
        width: 100%;
        top: 3rem;
        left: 0;
        right: 0;
        bottom: 0; }
    &__current {
        width: 22rem;
        margin: 2rem auto 0 auto;
        line-height: 3rem;
        background: $background-current-rule-game; }
    &__winner {
        font-size: 4.8rem;
        text-transform: uppercase;
        color: $colors-text-winner-victory; }
    &__score {
        display: inline-block;
        margin: 0 1rem 0 1rem;
        color: $colors-text-score-rule-game; }
    &_active {
        animation: run .3s ease-out .2s normal both; }
    &_close {
        animation: run .3s ease-in .2s reverse both; } }
//модальное окно победы
.victory {
    padding: 3rem;
    border-radius: 10rem;
    position: absolute;
    top: -4rem;
    left: 0;
    width: 100%;
    opacity: 1;
    height: 110%;
    z-index: 3;
    background: $background-modal-window;
    box-shadow: -5px 5px 14px #493543, 5px -5px 14px #614759;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &__text {
        width: 46%; }
    &__left {
        text-align: center;
        width: 20%; }
    &__right {
        text-align: center;
        width: 25%; }
    &__img-left {
        width: 17rem; }
    &__img-right {
        width: 25rem; }
    &__title {
        position: relative;
        left: 2%;
        text-transform: uppercase;
        font-size: 8rem;
        letter-spacing: 0.3rem;
        color: $colors-text-title-victory;
        &::after {
            content: "victory";
            position: absolute;
            top: 3.5%;
            left: -0.5%;
            color: $colors-text-victory; } }
    &__name {
        display: inline-block;
        position: relative;
        left: 12%;
        text-transform: uppercase;
        font-size: 8rem;
        letter-spacing: 0.3rem;
        color: $colors-text-name-victory; } }

//Help
.question {
    position: absolute;
    top: -4%;
    right: -12%;
    width: 10rem;
    text-align: center;
    &__icon {
        width: 10rem;
        cursor: pointer;
        opacity: 0;
        transition: 1.1s all;
        filter: $drop-shadow-question;
        margin-bottom: 2rem; }
    &__rules {
        width: 10rem;
        cursor: pointer;
        opacity: 1;
        transition: 1.1s all;
        filter: $drop-shadow-question;
        margin-bottom: 0rem; }
    &__rename {
        width: 10rem;
        cursor: pointer;
        opacity: 1;
        transition: 1.1s all;
        filter: $drop-shadow-question; } }

.icons_animated {
    transition: all .3s;
    &:hover {
        transform: scale(1.1); } }

